/* Resetting html/body heights for the flex container */
html, body {
  height: 100%;
  margin: 0;
  padding: 0;
}

body {
  font-family: sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;

  /* Dark theme background and text color */
  background: #121212;
  color: #ffffff;

  /* Enable flex layout */
  display: flex;
  flex-direction: column;
}

/* Advertisement header */
.ad-header {
  background: #1f1f1f;
  border-bottom: 1px solid #333;
  text-align: center;
  padding: 10px;
}

/* Main header */
.main-header {
  background: #202020;
  color: #ffffff;
  text-align: center;
  padding: 20px;
  border-bottom: 1px solid #333;
}

.main-header h1 {
  margin: 0;
  font-size: 1.5rem;
}

/* The main area grows to fill leftover space */
main {
  padding: 20px;
  flex: 1;
  border: 1px solid #333;
}

/* Chart container */
#race {
  border: 1px solid #444;
  max-width: 100vw;
  border: 1px solid #444;
  background-color: #1e1e1e;
}

/* Footer at the bottom */
footer {
  background: #1f1f1f;
  text-align: center;
  padding: 10px;
  margin-top: 20px;
  border-top: 1px solid #333;
}

/* Advertisement below chart */
.ad-below-chart {
  width: 100%;
  background: #1f1f1f;
  padding: 10px;
  text-align: center;
  margin-top: 20px;
  border-top: 1px solid #333;
}

.ad-below-chart p {
  margin: 0;
}

/* Basic responsiveness */
@media (max-width: 768px) {
  .main-header h1 {
    font-size: 1.2rem;
  }
  #race {
    height: 400px;
  }
}
